.addDoc select{
    margin-right: 10px;
    padding: 6px;
    font-size: 13.5px;
    min-width: 250px;
}
.addDoc .selectedUser{
    display:inline-block;
    margin: 0;
    padding:0;
    vertical-align:top;
    font-weight: 400;
}
.addDoc .selectedUserWithCancel{
    background: rgb(223, 243, 253);
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 5px;
    display: grid;
    grid-template-columns: 1fr auto;
    grid-template-rows: auto;
}
.addDoc .selectedUserWithCancel:hover{
    background: rgb(223, 243, 253);
    cursor: pointer;
}
.addDoc .deleteSelectedUser{
    padding: 0px;
    padding-left: 5px;
}
